/* Simulateurs.module.css */

.container {
  min-height: 100vh;
  background: #f5f5f5;
  padding: 0;
  margin-top: -60px !important; /* Compenser le padding-top de 60px du swipe-navigation-content */
  padding-top: 0 !important; /* FORCER padding-top  0 */
  position: relative;
  z-index: 1;
}

/* Sur mobile, ajuster le margin-top pour compenser le padding rduit */
@media (max-width: 768px), (hover: none) and (pointer: coarse) {
  .container {
    margin-top: -50px !important; /* Compenser le padding-top rduit (50px au lieu de 60px) */
  }
}

.content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
  padding-top: 0 !important; /* FORCER padding-top  0 pour viter tout espace */
}

/* APPROCHE RADICALE : Utiliser flexbox avec gap: 0 sur mobile pour supprimer TOUT espace */
@media (max-width: 768px), (hover: none) and (pointer: coarse) {
  .content {
    display: flex !important;
    flex-direction: column !important;
    gap: 0 !important; /* FORCER gap  0 pour supprimer tout espace entre les enfants */
  }
}

/* Sur mobile, supprimer le padding-top du conteneur pour viter l'espace aprs le header */
@media (max-width: 768px) {
  .content {
    padding-top: 0 !important;
  }
  
  /* Supprimer l'espace entre header et premier lment */
  .content > .header + * {
    margin-top: 0 !important;
    padding-top: 0 !important;
  }
}

/* Pour trs petits crans (iPhone) */
@media (max-width: 480px) {
  .content {
    padding-top: 0 !important;
  }
  
  /* Supprimer l'espace entre header et premier lment */
  .content > .header + * {
    margin-top: 0 !important;
    padding-top: 0 !important;
  }
}

/* Pour les appareils tactiles (iPhone, iPad) */
@media (hover: none) and (pointer: coarse) {
  .header {
    margin-bottom: 0 !important;
    box-shadow: none !important;
    border: none !important;
  }
  
  .content > .header + * {
    margin-top: 0 !important;
    padding-top: 0 !important;
  }
  
  .modeToggleBar {
    margin-top: 0 !important;
    padding-top: 0 !important;
  }
}

.header {
  background: linear-gradient(90deg, #1E3A8A 0%, #10B981 100%);
  color: white;
  padding: 2rem 0;
  margin-top: 60px !important; /* Commencer juste aprs la navigation fixe (60px de hauteur) */
  margin-bottom: 0 !important; /* Coller au formulaire */
  border-radius: 0; /* Pas de border-radius en haut */
  box-shadow: 0 4px 20px rgba(30, 58, 138, 0.3);
  position: relative;
  z-index: 1;
}

/* Header pour mode avanc - mme dgrad bleu vers vert */
.header.advancedMode {
  background: linear-gradient(90deg, #1E3A8A 0%, #10B981 100%);
  box-shadow: 0 4px 20px rgba(30, 58, 138, 0.3);
}

/* Sur mobile, la navigation fait aussi 60px (ou 50px selon les breakpoints) */
@media (max-width: 768px) {
  .header {
    margin-top: 60px !important; /* Navigation fait 60px sur mobile aussi */
  }
}

/* APPROCHE RADICALE : Forcer margin-bottom  0 et rduire le padding-bottom du header */
@media (max-width: 768px), (hover: none) and (pointer: coarse) {
  .header {
    margin-bottom: 0 !important;
    padding-bottom: 1rem !important; /* Rduire le padding-bottom sur mobile */
    padding-top: 2rem !important; /* Garder le padding-top */
    box-shadow: none !important;
    border: none !important;
  }
  
  /* Utiliser une marge ngative sur le premier lment pour "coller" au header */
  .content > .header + * {
    margin-top: 0 !important;
    padding-top: 0 !important;
  }
  
  .modeToggleBar {
    margin-top: 0 !important;
    padding-top: 0 !important;
  }
}

/* RGLES MOBILE - Doivent tre aprs la dclaration de base pour override */
@media (max-width: 768px) {
  .header {
    box-shadow: none !important;
    border: none !important;
    margin-bottom: 0 !important;
    margin-top: 0 !important;
  }
  
  /* Supprimer l'espace entre le header et le premier bloc - rgles trs spcifiques */
  .content .header {
    margin-bottom: 0 !important;
  }
  
  .content .header + .modeToggleBar,
  .content .header ~ .modeToggleBar {
    margin-top: 0 !important;
    padding-top: 0 !important;
  }
  
  .modeToggleBar {
    margin-top: 0 !important;
    padding-top: 0 !important;
  }
  
  /* Supprimer tout espace dans le conteneur aprs le header */
  .content > .header + *,
  .content > header + * {
    margin-top: 0 !important;
    padding-top: 0 !important;
  }
}

.headerContent {
  text-align: center;
  max-width: 800px;
  margin: 0 auto;
}

/* Rduire la largeur du headerContent sur mobile */
@media (max-width: 768px) {
  .headerContent {
    max-width: 95%;
    padding: 0 0.5rem;
  }
}

.title {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.subtitle {
  font-size: 1rem;
  opacity: 0.9;
  margin-bottom: 1rem;
  line-height: 1.5;
}

/* Informations du mode intgres dans le header */
.modeInfoInline {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 1.5rem;
  margin-top: 1.5rem;
  padding: 1.25rem;
  backdrop-filter: blur(10px);
  border-radius: 12px;
  margin-bottom: 0; /* Coller au formulaire */
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.2);
}

/* Mode simplifi - fond vert clair */
.modeInfoInline.simplifieMode {
  background: linear-gradient(135deg, #d1fae5 0%, #a7f3d0 100%);
  border: 2px solid rgba(16, 185, 129, 0.4);
}

/* Mode avanc - fond bleu clair */
.modeInfoInline.advancedMode {
  background: linear-gradient(135deg, #dbeafe 0%, #bfdbfe 100%);
  border: 2px solid rgba(59, 130, 246, 0.4);
}

.modeInfoContent {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  flex: 1;
}

.modeInfoTitle {
  font-size: 1.1rem;
  font-weight: 700;
}

/* Couleurs du texte selon le mode */
.modeInfoInline.simplifieMode .modeInfoTitle,
.modeInfoInline.simplifieMode .modeInfoTagline,
.modeInfoInline.simplifieMode .modeInfoStat {
  color: #065f46;
}

.modeInfoInline.advancedMode .modeInfoTitle,
.modeInfoInline.advancedMode .modeInfoTagline,
.modeInfoInline.advancedMode .modeInfoStat {
  color: #1e40af;
}

.modeInfoTagline {
  font-size: 0.95rem;
  font-weight: 500;
}

.modeInfoStats {
  display: flex;
  gap: 1rem;
  margin-top: 0.25rem;
}

.modeInfoStat {
  font-size: 0.85rem;
  display: flex;
  align-items: center;
  gap: 0.25rem;
}

.modeSwitchButtonInline {
  backdrop-filter: blur(10px);
  padding: 0.75rem 1.5rem;
  border-radius: 10px;
  font-weight: 600;
  font-size: 0.95rem;
  cursor: pointer;
  transition: all 0.2s ease;
  white-space: nowrap;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
}

/* Bouton pour mode simplifi */
.modeInfoInline.simplifieMode .modeSwitchButtonInline {
  background: linear-gradient(135deg, #10b981 0%, #059669 100%);
  border: 2px solid rgba(16, 185, 129, 0.5);
  color: white;
}

.modeInfoInline.simplifieMode .modeSwitchButtonInline:hover {
  background: linear-gradient(135deg, #059669 0%, #047857 100%);
  border-color: rgba(16, 185, 129, 0.7);
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(16, 185, 129, 0.3);
}

/* Bouton pour mode avanc */
.modeInfoInline.advancedMode .modeSwitchButtonInline {
  background: linear-gradient(135deg, #3b82f6 0%, #2563eb 100%);
  border: 2px solid rgba(59, 130, 246, 0.5);
  color: white;
}

.modeInfoInline.advancedMode .modeSwitchButtonInline:hover {
  background: linear-gradient(135deg, #2563eb 0%, #1d4ed8 100%);
  border-color: rgba(59, 130, 246, 0.7);
  transform: translateY(-1px);
  box-shadow: 0 4px 12px rgba(59, 130, 246, 0.3);
}

/* Responsive pour modeInfoInline */
@media (max-width: 768px) {
  .modeInfoInline {
    flex-direction: column;
    align-items: stretch;
    gap: 1rem;
    padding: 1rem;
    max-width: 90%; /* Rduire la largeur  90% */
    margin-left: auto;
    margin-right: auto;
  }

  .modeSwitchButtonInline {
    width: 100%;
    padding: 0.875rem 1.25rem;
  }

  .modeInfoStats {
    flex-direction: column;
    gap: 0.5rem;
  }
}

.modeToggleBar {
  background: white;
  border-radius: 16px;
  padding: 1.5rem 2rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  margin-bottom: 1rem;
  margin-top: 0 !important; /* FORCER margin-top  0 par dfaut */
  display: flex;
  align-items: center;
  justify-content: space-between;
  border: 1px solid #e2e8f0;
  gap: 1.5rem;
}

.modeContext {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  background: white;
  border-radius: 12px;
  padding: 1.25rem;
  border: 1px solid #e2e8f0;
  transition: all 0.3s ease;
}

/* Style spcial pour le mode avanc */
.modeContextAdvanced {
  background: linear-gradient(135deg, #60a5fa 0%, #3b82f6 50%, #2563eb 100%) !important;
  border: 2px solid rgba(59, 130, 246, 0.4) !important;
  box-shadow: 0 4px 20px rgba(96, 165, 250, 0.35) !important;
  color: white;
}

/* Style spcial pour le mode simplifi - Mme vert que le header */
.modeContextSimplifie {
  background: linear-gradient(135deg, #0f766e 0%, #134e4a 100%) !important;
  border: 2px solid #134e4a !important;
  box-shadow: 0 4px 20px rgba(15, 118, 110, 0.3) !important;
  color: white;
}

.modeBadge {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  font-weight: 600;
  font-size: 0.95rem;
  padding: 0.4rem 0.9rem;
  border-radius: 999px;
  width: fit-content;
}

.modeBadgeSimplifie {
  background: rgba(255, 255, 255, 0.25);
  color: white;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.modeBadgeAdvanced {
  background: rgba(255, 255, 255, 0.25);
  color: white;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.modeTagline {
  font-size: 1.05rem;
  color: #1f2937;
  margin: 0;
  font-weight: 500;
}

/* Style pour le tagline en mode avanc */
.modeContextAdvanced .modeTagline {
  color: white !important;
  font-weight: 600;
}

/* Style pour le tagline en mode simplifi */
.modeContextSimplifie .modeTagline {
  color: white !important;
  font-weight: 600;
}

.modeStats {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  font-size: 0.95rem;
  color: #6b7280;
}

/* Style pour les stats en mode avanc */
.modeContextAdvanced .modeStats {
  color: rgba(255, 255, 255, 0.9) !important;
}

/* Style pour les stats en mode simplifi */
.modeContextSimplifie .modeStats {
  color: rgba(255, 255, 255, 0.9) !important;
}

.modeStat {
  display: inline-flex;
  align-items: center;
  gap: 0.35rem;
  background: rgba(255, 255, 255, 0.15);
  padding: 0.4rem 0.75rem;
  border-radius: 8px;
  backdrop-filter: blur(10px);
  font-weight: 500;
}

.modeSwitchButton {
  color: white;
  border: none;
  padding: 0.95rem 1.5rem;
  border-radius: 12px;
  font-weight: 600;
  font-size: 0.95rem;
  cursor: pointer;
  box-shadow: 0 10px 25px rgba(15, 23, 42, 0.15);
  transition: transform 0.2s ease, box-shadow 0.2s ease, filter 0.2s ease;
}

.modeSwitchAdvanced {
  background: linear-gradient(135deg, #60a5fa 0%, #3b82f6 50%, #2563eb 100%);
  box-shadow: 0 10px 25px rgba(96, 165, 250, 0.3);
}

.modeSwitchSimplifie {
  background: linear-gradient(135deg, #1E3A8A 0%, #10B981 100%);
  box-shadow: 0 10px 25px rgba(30, 58, 138, 0.25);
}

.modeSwitchButton:hover {
  transform: translateY(-1px);
  filter: brightness(1.05);
}

.modeSwitchButton:active {
  transform: translateY(0);
  filter: brightness(0.98);
}

.modeHint {
  margin-bottom: 2rem;
  color: #475569;
  font-size: 1rem;
  line-height: 1.6;
  background: white;
  border-radius: 12px;
  padding: 1rem 1.5rem;
  border: 1px solid #e2e8f0;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.05);
}

.formContainer {
  background: white;
  border-radius: 0 0 16px 16px; /* Arrondir seulement en bas */
  padding: 2rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  margin-top: 0 !important; /* Coller au header */
  margin-bottom: 3rem;
  border: 1px solid #e9ecef;
  border-top: none; /* Pas de bordure en haut pour coller au header */
}

.howItWorks {
  background: white;
  border-radius: 16px;
  padding: 2rem;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  margin-bottom: 3rem;
}

.sectionTitle {
  font-size: 2rem;
  font-weight: 600;
  color: #0f766e;
  margin-bottom: 1rem;
}

/* Style pour le mode avanc */
.sectionTitle.advanced {
  color: #3b82f6;
}

.explanation {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.explanation p {
  color: #6c757d;
  line-height: 1.6;
  font-size: 1.1rem;
}

.guideLink {
  color: #3498db;
  text-decoration: none;
  font-weight: 500;
  font-size: 1.1rem;
  transition: color 0.3s ease;
}

.guideLink:hover {
  color: #2980b9;
  text-decoration: underline;
}

.loadingOverlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.loadingSpinner {
  width: 50px;
  height: 50px;
  border: 4px solid #f3f3f3;
  border-top: 4px solid #3498db;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 1rem;
}

.loadingOverlay p {
  color: white;
  font-size: 1.2rem;
  font-weight: 500;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Responsive */
@media (max-width: 768px) {
  .container {
    padding: 1rem 0;
  }
  
  .title {
    font-size: 2.5rem;
  }
  
  .subtitle {
    font-size: 1.1rem;
  }
  
  .formContainer,
  .howItWorks {
    padding: 1.5rem;
    margin-top: 0 !important;
    border-radius: 0;
    border-top: none;
  }
  
  .sectionTitle {
    font-size: 1.75rem;
  }

  .modeToggleBar {
    flex-direction: column;
    align-items: flex-start;
  }

  .modeSwitchButton {
    width: 100%;
    justify-content: center;
  }
}

@media (max-width: 480px) {
  .header {
    box-shadow: none !important;
    border: none !important;
    margin-bottom: 0 !important; /* Pas de margin-bottom sur trs petits crans */
  }
  
  /* Supprimer l'espace entre le header et le premier bloc */
  .modeToggleBar {
    margin-top: 0 !important;
    padding-top: 0 !important;
  }
  
  /* Supprimer tout espace dans le conteneur aprs le header */
  .content > .header + * {
    margin-top: 0 !important;
    padding-top: 0 !important;
  }
  
  .content {
    padding: 0 0.5rem;
  }
  
  .title {
    font-size: 2rem;
  }

  .formContainer,
  .howItWorks {
    padding: 1rem;
  }

  .modeStats {
    gap: 0.6rem;
  }
}
