/* InputField.module.css */

.fieldGroup {
  margin-bottom: 1.5rem;
}

.label {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-weight: 600;
  color: #374151;
  margin-bottom: 0.75rem;
  font-size: 1rem;
}

.labelIcon {
  font-size: 1.2rem;
}

.labelTooltip {
  margin-left: auto;
}

.inputContainer {
  position: relative;
  display: flex;
  align-items: center;
}

.input {
  width: 100%;
  padding: 1rem;
  border: 2px solid #93c5fd; /* Bordure bleue pour distinguer les champs */
  border-radius: 8px;
  font-size: 1rem;
  transition: all 0.3s ease;
  background: #dbeafe; /* Bleu clair distinct pour les champs de saisie */
  color: #1e3a8a; /* Texte bleu fonc pour meilleur contraste */
}

.input:focus {
  outline: none;
  border-color: #2563eb;
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.15);
  background: #bfdbfe; /* Bleu lgrement plus fonc au focus */
}

.input.error {
  border-color: #dc2626;
  box-shadow: 0 0 0 3px rgba(220, 38, 38, 0.1);
}

.input.success {
  border-color: #2563eb;
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.1);
}

.inputTooltip {
  position: absolute;
  right: 0.75rem;
  top: 50%;
  transform: translateY(-50%);
  z-index: 10;
}

.tooltipIcon {
  font-size: 1rem;
  cursor: help;
  color: #6c757d;
  transition: color 0.2s ease;
}

.tooltipIcon:hover {
  color: #2563eb;
}

.calculatorButton {
  position: absolute;
  right: 0.75rem;
  top: 50%;
  transform: translateY(-50%);
  background: #f8f9fa;
  border: 1px solid #e9ecef;
  border-radius: 6px;
  padding: 0.5rem;
  cursor: pointer;
  font-size: 1rem;
  transition: all 0.2s ease;
  z-index: 10;
}

.calculatorButton:hover {
  background: #e9ecef;
  border-color: #2563eb;
  color: #2563eb;
}

/* Slider styles */
.sliderContainer {
  margin-bottom: 1rem;
}

.slider {
  width: 100%;
  height: 8px;
  border-radius: 4px;
  background: #e9ecef;
  outline: none;
  -webkit-appearance: none;
  margin-bottom: 1rem;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background: #2563eb;
  cursor: pointer;
  box-shadow: 0 2px 6px rgba(37, 99, 235, 0.3);
}

.slider::-moz-range-thumb {
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background: #2563eb;
  cursor: pointer;
  border: none;
  box-shadow: 0 2px 6px rgba(37, 99, 235, 0.3);
}

.sliderLabels {
  display: flex;
  justify-content: space-between;
  font-size: 0.875rem;
  color: #6c757d;
  margin-bottom: 0.5rem;
}

.sliderValue {
  text-align: center;
  font-size: 1.5rem;
  font-weight: 700;
  color: #2563eb;
}

/* Messages */
.statusMessage,
.helpMessage {
  margin-top: 0.75rem;
}

/* Responsive */
@media (max-width: 768px) {
  .label {
    font-size: 1rem;
    font-weight: 600;
  }
  
  .input {
    padding: 1.25rem;
    font-size: 1.125rem; /* Plus gros sur mobile */
    border-width: 2px;
    width: 100%;
    box-sizing: border-box;
    background: #dbeafe !important; /* Bleu clair pour mode avanc - force sur mobile */
    border-color: #93c5fd !important; /* Bordure bleue - force sur mobile */
    color: #1e3a8a !important; /* Texte bleu fonc - force sur mobile */
  }
  
  /* Clavier numrique pour montants */
  .input[type="text"][inputMode="numeric"],
  .input[type="number"] {
    font-size: 1.25rem;
  }
  
  .sliderValue {
    font-size: 1.5rem;
  }
  
  .calculatorButton {
    padding: 0.5rem;
    font-size: 1rem;
    min-width: 48px;
    min-height: 48px;
  }
  
  .fieldGroup {
    margin-bottom: 2rem;
  }
  
  /* Un champ par cran sur mobile */
  .fieldGroup:not(:first-of-type) {
    margin-top: 1.5rem;
  }
}
